.backdrop {
    opacity: 1;
    pointer-events: visible;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    overflow-y: scroll;
    //background-color: rgba(255, 165, 0, 0.5);
    background-color: rgba(0, 0, 0, 0.2);
    transition: opacity $transition;

    @media screen and (max-width: 767px) {
        padding: 93px 15px 94px 15px;
    }


    &.is--hidden {
        opacity: 0;
        pointer-events: none;
    }

    &.is--hidden .pr-modal {
        transform: translate(-50%, -50%) scale(1.1);
    }
}

.pr-modal {
    position: absolute;
    width: 80%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    overflow: hidden;
    max-height: 95vh;
    //height:100%;

    padding: 25px;
    background-color:white;

    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
        0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 24px;

    @media screen and(min-width:480px) {
        max-width: 360px;
            max-height: 489px;
            width: 100%;
            height: 100%;
        padding-left: 30px;
        padding-right: 30px;
    }

    @media screen and(min-width:768px) {
        // max-width: 224px;
        //     max-height: 473px;
        //     width: 100%;
        //     height: 100%;
        max-width: 395px;
            max-height: 600px;
            width: 100%;
            height: 100%;
    }

    @media screen and(min-width:1200px) {
        max-width: 395px;
            max-height: 600px;
            width: 100%;
            height: 100%;
    }

    &.pr-modal--ice-cream{
        background-color: $primary-elements-bg-color;
    }

    &.pr-modal--ice-coffee {
        background-color: $secondary-elements-bg-color;
    }

    &.pr-modal--milkshakes {
        background-color: $third-elements-bg-color;
    }
}

.pr-modal__title {
    position: relative;
    // color: orange;
    font-size: 26px;
    padding-bottom: 180px;
    margin-bottom: 0;
    text-align: center;

    font-family: 'Titan One';
        font-weight: 400;
        //font-size: 20px;
        line-height: 1.15;
       
        letter-spacing: 0.04em;
        text-transform: uppercase;
    
        color: $secondary-text-color;

    @media screen and(min-width:480px) {
        padding-bottom: 210px;
    }

    @media screen and(min-width:768px) {
        font-size: 36px;
        padding-bottom: 270px;
    }

    @media screen and(min-width:1200px) {
        
        padding-bottom: 280px;
    }
}

