.pr-modal__btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-left: auto;
    margin-bottom: 5px;

    width: 30px;
    height: 30px;
    padding: 1px 6px;
    border-color: transparent;
    border-width: 2px;
    border-radius: 50%;
    
   background-color: $social-icon-hover-color;
    transition: background-color $transition;

    &:hover,
    &:focus {
        background-color: $social-icon-color;
    }
}

.pr-modal__btn-icon {
    
    fill: $social-icon-color;
    transition: fill $transition;

    .pr-modal_btn:hover &,
    .pr-modal_btn:focus & {
        fill: $social-icon-hover-color;
    }
}

.pr-modal__text {
    margin-bottom: 30px;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.5;
    text-align: center;
    letter-spacing: 0.04em;
    color: $secondary-text-color;

    @media screen and(min-width:480px) {
        margin-bottom: 50px;
        font-size: 16px;
    }

    @media screen and(min-width:768px) {
        line-height: 1.79;
        margin-bottom: 20px;
    }

    @media screen and(min-width:1200px) {
        font-size: 18px;
        line-height: 1.75;
        margin-bottom: 10px;
    }
}

